modal-container-error {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal-error {
  position: relative;
  width: 320px;
  margin: auto;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0px 7px 15px rgba(0, 1, 1, 0.15);
}
.modal-error__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 45px 42px 35px;
}

.modal-error__title{
  .font(18px, 26px, 700);
  color: @color-title;
  text-transform: uppercase;
  text-align: center
}

.modal-error__description {
  .font(14px, 21px, 400);
  color: @color-description;
  text-transform: uppercase;
  text-align: center;
}

.modal-error__button {
  padding: 18px 64px 14px;
  .font(18px, 26px, 700);
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  background-color: @blue;
}


@media (min-width: @tablet-width) {
  .modal-error {
    width: 400px;
  }

  .modal-error__content {
    padding: 50px 46px 48px;
  }

  .modal-error__title{
    .font(30px, 40px, 700);
  }

  .modal-error__button {
    padding: 18.5px 108px 13.5px;
  }
}

@media (min-width: @desktop-width) {
  .modal-error__content {
    padding: 60px 46px 48px;
  }

  .modal-error__title{
    .font(30px, 40px, 700);
  }
}
